 import React from 'react';
 import { Button } from 'semantic-ui-react';
 import { Link } from "react-router-dom";

const Layout = () => {
  return (
    <>
      <nav>
         <ul> 
          <Link to="/Read">
             <Button > Read </Button> 
           </Link>
            <Link to="/Update">
              <Button >Update</Button>
            </Link> 
            </ul> 
       </nav>
     </>
   );
};

export default Layout;
